.BidTable {
  position: absolute;
  top: 15vh;
}

.BidTable__Name {
  display: inline-block;
  width: 30vh;
  text-align: center;
  margin: 0 2vh 2vh 2vh;
  font-size: 6vh;
  color: #ddd;
}

.BidTable__Bid {
  display: inline-block;
  width: 30vh;
  border-radius: 2vh;
  text-align: center;
  margin: 2vh;
  font-size: 10vh;
  color: #ddd;
  background-color: darkblue;
}

.BidTable__Bid--tentative {
  -webkit-animation: 0.5s infinite alternate blink-green;
          animation: 0.5s infinite alternate blink-green;
}

@-webkit-keyframes blink-green {
  from {
    -webkit-box-shadow: none;
            box-shadow: none;
  }

  to {
    -webkit-box-shadow: 0 0 10vh 1vh lime;
            box-shadow: 0 0 10vh 1vh lime;
  }
}

@keyframes blink-green {
  from {
    -webkit-box-shadow: none;
            box-shadow: none;
  }

  to {
    -webkit-box-shadow: 0 0 10vh 1vh lime;
            box-shadow: 0 0 10vh 1vh lime;
  }
}



