.HomeScreen__Join,
.HomeScreen__Create {
  position: fixed;
  top: 0;
  width: 50vw;
  height: 100vh;
  text-align: center;
  line-height: 100vh;
  font-size: 10vh;
  color: #EEE;
}

.HomeScreen__Join {
  left: 0;
  background-color: #3aa3dd;
}

.HomeScreen__Create {
  left: 50vw;
  background-color: green;
}

.HomeScreen__PendingOverlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #444;
  opacity: 0.5;
}

/* https://github.com/lukehaas/css-loaders */
.HomeScreen__PendingIcon,
.HomeScreen__PendingIcon:before,
.HomeScreen__PendingIcon:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation: homescreen-load 1.8s infinite ease-in-out;
          animation: homescreen-load 1.8s infinite ease-in-out;
}

.HomeScreen__PendingIcon {
  color: #ffffff;
  font-size: 3vh;
  position: fixed;
  top: 50vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}

.HomeScreen__PendingIcon:before,
.HomeScreen__PendingIcon:after {
  content: '';
  position: absolute;
  top: 0;
}

.HomeScreen__PendingIcon:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}

.HomeScreen__PendingIcon:after {
  left: 3.5em;
}

@-webkit-keyframes homescreen-load {
  0%,
  80%,
  100% {
    -webkit-box-shadow: 0 2.5em 0 -1.3em #888;
            box-shadow: 0 2.5em 0 -1.3em #888;
  }

  40% {
    -webkit-box-shadow: 0 2.5em 0 0 #ffffff;
            box-shadow: 0 2.5em 0 0 #ffffff;
  }
}

@keyframes homescreen-load {
  0%,
  80%,
  100% {
    -webkit-box-shadow: 0 2.5em 0 -1.3em #888;
            box-shadow: 0 2.5em 0 -1.3em #888;
  }

  40% {
    -webkit-box-shadow: 0 2.5em 0 0 #ffffff;
            box-shadow: 0 2.5em 0 0 #ffffff;
  }
}



